html {
    min-height: 100%;
  }
  
  body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100%;
  }
  
  body {
    position: absolute;
    width: 80%;
    left: 10%;
    top: 5%;
  }
  
  div.banner {
    padding: 20px;
    margin-top: 60px;
  }
  
  form {
    padding: 20px;
    margin-top: 60px;
  }
  
  form button, form h5 {
    margin: 20px 0;
  }
  
  input[type=text]:not(.browser-default):focus:not([readonly]),
  input[type=email]:not(.browser-default):focus:not([readonly]),
  input[type=password]:not(.browser-default):focus:not([readonly]),
  textarea.materialize-textarea:focus:not([readonly]){
    border-color: #ec407a;
    box-shadow: none;
  }
  
  input[type=text]:not(.browser-default):focus:not([readonly]) + label,
  textarea.materialize-textarea:focus:not([readonly]) + label,
  input[type=email]:not(.browser-default):focus:not([readonly]) + label,
  input[type=password]:not(.browser-default):focus:not([readonly]) + label{
    color: #ec407a !important;
  }
  
  .flex {
    display: flex;
    flex-wrap: wrap;
  }

  .no_padding {
    padding: 0 !important;
  }

  .clickable:hover {
    cursor: pointer;
  }

  .moveable:hover {
    cursor: -webkit-grab;
    cursor: grab;
  }

  .handle {
    width:12px !important;
    height:12px !important;
    border: 2px solid black;
    background-color: white;
  }

  .control_property_bar {
    overflow-y: scroll;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
}
  .control_property_bar::-webkit-scrollbar { /* WebKit */
    width: 0;
    height: 0;
}
:root {
    /* 
        FIRST WE'LL DEFINE OUR SWATCHES, i.e. THE COMPLEMENTARY
        COLORS THAT WE'LL USE TOGETHER IN MULTIPLE PLACES THAT
        TOGETHER WILL MAKE UP A GIVEN THEME
    */
    --swatch-foundation:    #EEEEDD; /* USED FOR BODY BACKGROUND */
    --swatch-primary:       #E6E6E6; /* DOMINANT COLOR, USED FOR MOST BACKGROUNDS */
    --swatch-complement:    #E1E4CB; /* USED IN FORGROUND OF PRIMARY FOR TEXT AND BORDERS */
    --swatch_contrast:      #111111; /* USED IN MOUSE-OVER SELECTION OF LARGE ELEMENTS */
    --swatch-accent:        #669966; /* USED IN MOUSE-OVER SELECTION OF TEXT AND BORDERS */    
    --my-font-family:          "Robaaaoto";
    --bounceEasing: cubic-bezier(0.51, 0.92, 0.24, 1.15);
  }
  
  body {
    background-color: #473850;
    font-family: 'Lexend Exa';
  }
  
  div.banner {
    background-image: -webkit-gradient(linear,         
    left top, left bottom,         
    from(#FF5252), to(#7F5A95));
    background-image: linear-gradient(to bottom,         
    #FF5252, #7F5A95);
    color: white;
    font-size: 48pt;
    border-color: black;
    border-width: 2px;
    border-style: solid;
    border-radius: 10px;
    padding-top: 5%;
    padding-bottom: 5%;
  }
  
  .home_new_wireframe_button {
    font-size: 32pt;
  }
  .home_new_wireframe_button:hover {
    box-shadow: 1px 2px;
  }
  
  .rounded {
    border-radius: 10px;
  }
  
  .padding_20 {
    padding-right: 20px;
    padding-left: 20px;
  }
  
  .padding_12 {
    padding-right: 12px;
    padding-left: 12px;
  }
  
  .margin_bottom_-10 {
    margin-bottom: -10px !important;
  }
  
  .wireframe_link:hover {
    background-color: #7F5A95;
    color: white !important; 
    cursor: pointer;
  }
  
  .text_20 {
    font-size: 20px !important;
  }
  
  .text_16 {
    font-size: 16px !important;
  }

  /* label focus color */
  .input-field input + label {
    color: #000 !important;
  }
  /* label underline focus color */
  .input-field input {
    border-bottom: 1px solid #000 !important;
    box-shadow: 0 1px 0 0 #000 !important;
  }

  .material-tooltip {
    -webkit-transform: translateY(-30px) translateX(-160px) !important;
            transform: translateY(-30px) translateX(-160px) !important;
  }

  .disabled:hover {
    cursor: not-allowed;
  }

  .delete_wireframe {
    color: black !important;
    border-left: 2px solid lightgray;
    padding-left: 15px;
    margin-right: -15px;
    padding-right: 15px;
  }

  .delete_wireframe:hover {
    color: red !important;
  }
